import React from 'react';
import axios from 'axios';
class Search extends React.Component {
    static onSearchButtonClick = function(e,isFromLandingPage) {
        let inputText = isFromLandingPage ? e.target : (e.target.offsetParent.children.item(0) ||
                        e.target.offsetParent.children.item[0])
        
        let retObj = {};
        if((inputText.value||"").replace(/\s/g, '')==="")
        {
            retObj={
                        value:"Enter valid Text to search",
                        canSearch: false
                    };
        }
        else
        {
            retObj={
                value:inputText.value,
                canSearch: true
            };
        }
        return retObj;
    };

    static onTagButtonClick = function(e) {
        let inputText = e.target ? e.target.value : "";
        alert(inputText);
    };

    static getResults = function(searchKey)
    {
        let baseURL = 'https://azsrch-cognitivesearch.search.windows.net/indexes/azsearch-index/docs?api-version=2019-05-06&';
        return new Promise((resolve, reject) => {
            axios({
                method: 'GET',
                url: baseURL+searchKey,
                contentType: "application/json",
                async: false,
                headers:{
                  'api-key':'D5BF0358B93DEAC8C8C4979342386611'
                }
              })
              .then((res)=>{
                resolve(res.data);
              }).catch(
                err=>{
                  console.log(err);
                  this.showOrHideLoader(false);
                  reject();
                }
              )
          })    
    }

  static onclickFilters(subFilters, event, element){
      var currentElement = document.getElementById(element);
      var activeClass = "is-checked";
      currentElement.classList.contains(activeClass) ? 
                  currentElement.classList.remove(activeClass) : currentElement.classList.add(activeClass);
      alert(subFilters);
  }

    static showOrHideLoader(showLoader)
    {
      showLoader ? 
      document.body.classList.add('loading-indicator') :
      document.body.classList.remove('loading-indicator')
    }
}
export default Search;