import React from 'react';
import search from '../common/search';
function tagEntities(props) {   
    const tagData = props.tags;    
    let classInfo = props.iskeyPhrase ? "tag tag-entities tag-keyphrases" : "tag tag-entities";
        return (  
            <p>          
            {tagData.slice(0, 10).map((tag,index) => (
                <button key={"tag"+index} className={classInfo} value={tag}
                onClick={(e)=>search.onTagButtonClick(e)}>{tag}</button>
            ))}
            </p>
        );
    }
  export default tagEntities;